import React from "react";
import { Button, Card, Form } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import { AiFillEye } from "react-icons/ai";
import { Link, useNavigate } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import "react-datepicker/dist/react-datepicker.css";
import { PATH } from "../../../../utils/path";

export default function FormsDatatable() {
  const navigate = useNavigate();

  const handleButtonClick = () => {
    navigate(PATH.SUPERADMIN_QUESTIONNAIRE);
  };

  const columns = [
    {
      dataField: "id",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "diseaseName",
      text: "Disease Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "createdDate",
      text: "Created Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row) => (
        <Link
          to={navigate(`/superadmin/forms/questionnaire?disease=${11}`)}
          className="table-action"
        >
          <AiFillEye /> View
        </Link>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const data = [
    {
      id: <p className="table-bold-text m-0">#111</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#112</p>,
      diseaseName: <p className="m-0 table-bold-text">Malaria</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#113</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
    {
      id: <p className="table-bold-text m-0">#124</p>,
      diseaseName: <p className="m-0 table-bold-text">Acne</p>,
      createdDate: <p className="m-0 table-normal-text">4/21/12</p>,
    },
  ];
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };
  return (
    <>
      <Card className="shadow-sm patient-card patient-profile-wrapper">
        <>
          <div className="d-flex justify-content-between mt-3 mx-3">
            <span className="d-flex">
              <Form.Control
                type="text"
                placeholder="Search"
                className="me-2 mb-3 search-field-spacing"
                aria-label="Search"
              />
              <BiSearch size={22} className="searchbar-icon" />
            </span>
            <span>
              <Button
                variant="primary"
                type="button"
                className="Admin-Add-btn fw-bold"
                onClick={handleButtonClick}
              >
                Add
              </Button>
            </span>
          </div>
          <span className="doctor-datatable">
            {data.length > 0 ? (
              <BootstrapTable
                columns={columns}
                data={data}
                keyField="id"
                id="bar"
                pagination={paginationFactory(paginateOptions)}
                // striped
                bordered={false}
                wrapperClasses="table-responsive"
              />
            ) : (
              <p className="text-center">No Record Found</p>
            )}
          </span>
        </>
      </Card>
    </>
  );
}
